import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
//import Heder from "components/Heder"
import mainPageStyles from 'assets/jss/styled-components'
import ItemCard from 'components/ItemCard'

class HomePage extends React.Component {


    constructor(props) {
        super(props);

        let { locale } = props;
        let urlLocale = props.location.pathname.substring(1, 3)
        if (locale !== urlLocale) {
            props.setLocale(urlLocale)
            locale = urlLocale
        }
        //this.items = content.heder.item;
    };
    preventDefault = event => event.preventDefault();

    render() {
        const { classes } = this.props;
        let { content } = this.props;

        return (

            <div className={classes.container}>

                <Grid container spacing={3}>


                    <Grid item xs={12} >
                        <Paper className={classes.paper}>
                            <ItemCard content={content}></ItemCard>

                        </Paper>
                    </Grid>
                    <Grid item xs={6}>
                        <Paper className={classes.paper}>
                            <ItemCard content={content}></ItemCard>
                        </Paper>
                    </Grid>
                    <Grid item xs={6}>
                        <Paper className={classes.paper}>
                            <ItemCard content={content}></ItemCard>
                            </Paper>
                    </Grid>
                    {/* <Grid item xs={3}>
                        <Paper className={classes.paper}>xs=3</Paper>
                    </Grid>
                    <Grid item xs={3}>
                        <Paper className={classes.paper}>xs=3</Paper>
                    </Grid>
                    <Grid item xs={3}>
                        <Paper className={classes.paper}>xs=3</Paper>
                    </Grid>
                    <Grid item xs={3}>
                        <Paper className={classes.paper}>xs=3</Paper>
                    </Grid> */}
                </Grid>
            </div>
        )
    }
}
export default withStyles(mainPageStyles)(HomePage);