import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import mainPageStyles from 'assets/jss/styled-components'
import img from 'assets/img/test.jpg';

class ItemCard extends React.Component {


    render() {
        const { classes } = this.props;
        const { content } = this.props;
        // console.log("it prop",this.props)
        return (

            <div className={classes.text}>
                <h1>Welcome to the   {content.heder.menu.item[0]}</h1>
                <img className={classes.itemCard} src={img} alt="Item" />
            </div>
        )
    }
}
export default withStyles(mainPageStyles)(ItemCard);