import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import mainPageStyles from 'assets/jss/styled-components'

class WarrantyPage extends React.Component {


    constructor(props) {
        super(props);

        let { locale } = props;
        let urlLocale = props.location.pathname.substring(1, 3)
        if (locale !== urlLocale) {
            props.setLocale(urlLocale)
            locale = urlLocale
        }
        //this.items = content.heder.item;
    };
    preventDefault = event => event.preventDefault();

    render() {
        const { classes } = this.props;
        let { content } = this.props;
      
        return (

            <div className={classes.text}>
                <h1>Welcome to the   {content.heder.menu.item[1]}</h1>
            </div>
        )
    }
}
export default withStyles(mainPageStyles)(WarrantyPage);