import React from 'react';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import mainPageStyles from 'assets/jss/styled-components';
import { withStyles } from '@material-ui/core/styles';



class LangButton extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectetItem: 0,
            content: props.content
        }
        // this.content = props;
        this.lang = this.state.content.heder.comp.languageSwitch;

    };






    setLocale = (newLocale) => {
        // const { locale } = this.props
        // let url = '/' + newLocale + this.props.location.pathname.substring();
        // this.props.history.push(url);
        //console.log(this.props)
        this.props.setLocale(newLocale);


    }
    onSelectItem(event) {
        console.log(event.target.value);
        switch (event.target.value) {
            case 0:
                this.setLocale('ua');
                this.setState({ selectetItem: 0 })
                // this.setState({content: this.props.content});
                break;
            case 1:
                this.setLocale('ru');
                this.setState({ selectetItem: 1 })
                // this.setState({content: this.props.content});
                break;
            default:
                this.setLocale('ua');
                this.setState({ selectetItem: 0 })
            // this.setState({content: this.props.content});
        }

    }

    render() {
        const { classes } = this.props;
        const lang = this.props.content.heder.comp.languageSwitch;
        return (

            <div>
                {/* <div>
                    <button
                        onClick={() => this.setLocale('ru')}
                    >ru</button> | <button
                        onClick={() => this.setLocale('en')}
                    >en</button> | <button
                        onClick={() => this.setLocale('ua')}
                    >ua</button>
                </div> */}

                <div>
                    <FormControl>
                        <Select className={classes.lang}
                            id="lang-select"
                            value={this.state.selectetItem}
                            onChange={this.onSelectItem.bind(this)}
                        >
                            {lang.map((item, index) => (
                                <MenuItem key={index} name={item} value={index}>{item}</MenuItem>
                            ))}
                        </Select>

                    </FormControl>
                </div>
            </div>

        )
    }
}

export default withStyles(mainPageStyles)(LangButton);