
import React from 'react'
import { Route, Redirect, Switch } from 'react-router-dom'
import Heder from "components/Heder"
//import MainPage from "../views/MainPage.js"
import NoFound from "../views/NoFoundPage.js"
import contentUa from 'assets/data/contentUa.json';
import contentRu from 'assets/data/contentRu.json';



class LangRouter extends React.Component {
  constructor(props) {
    super(props)

    let locale = window.localStorage.getItem('locale')
    if (locale === null) {
      window.localStorage.setItem('locale', 'ua')
      locale = 'ua'
    }
    this.state = {
      locale: locale,
      content: contentUa
    }
    this.content = contentUa;
  }

  setLocale = (newLocale) => {
    console.log('newLocale = ', newLocale);
    window.localStorage.setItem('locale', newLocale)
    this.setState({ locale: newLocale })
    switch (newLocale) {
      case 'ua':
        this.setState({ content: contentUa });
      
        break;
      case 'ru':
        this.setState({ content: contentRu });
      
        break;
      default:
        this.setState({ content: contentUa });
    }
  }

  render() {
    const { locale } = this.state;
    const { content } = this.state;
    return (
      <Switch>
        <Route path="/" exact render={propRouter => {
          return <Redirect to={"/home"} />
        }} />
        
        <Route path="/*" render={
          propsRouter =>
            <Heder
              {...propsRouter}
              locale={locale}
              content={content}
              setLocale={this.setLocale}
            />
        } />
        {/* <Route path="/*" render={
          propsRouter =>
            <Heder
              {...propsRouter}
              locale={locale}
              content={content}
              setLocale={this.setLocale}
            />
        } /> */}
        <Route path="*" render={
          propsRouter =>
            <NoFound
              {...propsRouter}
              locale={locale}
              content={content}
              setLocale={this.setLocale}

            />

        } />
      </Switch>
    )
  }
}


export default LangRouter;