import React from 'react';
import { Route, Link, Switch } from 'react-router-dom';
//import Main from '../views/MainPage'
import Home from "../views/HomePage";
import WarrantyPage from "../views/WarrantyPage";
import Contacts from "../views/ContactsPage";
import NoFound from "../views/NoFoundPage";
import LangButtons from "components/LangButton";
import { withStyles } from '@material-ui/core/styles';
import mainPageStyles from 'assets/jss/styled-components';
import img from 'assets/img/tab.png';
import Grid from '@material-ui/core/Grid';



const Heder = (props) => {
  console.log(props);
  const { classes } = props;
  // let { locale } = props
  let { content } = props
  // let urlLocale = props.location.pathname.substring(1, 3)
  // if (locale !== urlLocale) {
  //   props.setLocale(urlLocale)
  //   locale = urlLocale
  // }
  return (
    <div className={classes.paper}>
       <Grid container spacing={3}>
    <div className={classes.container}>
      <div className={classes.heder}>



        <div className={classes.container2}></div>

        <div className={classes.langButton}>
          <LangButtons {...props} />
        </div>

        <div className={classes.container2}>
          <img className={classes.logo} src={img} alt="Logo" />
          <h1 className={classes.logoText}>{content.heder.comp.caption}</h1>
        </div>

        <div className={classes.hederNav}>
          <Link className={classes.link} to={ "/home"}>{content.heder.menu.item[0]}</Link>
          <Link className={classes.link} to={ "/warranty"} >{content.heder.menu.item[1]}</Link>
          <Link className={classes.link} to={ "/contacts"} >{content.heder.menu.item[2]}</Link>
          <Link className={classes.link} to={ "/description"}>{content.heder.menu.item[3]}</Link>
          <Link className={classes.link} to={ "/wholesale"}>{content.heder.menu.item[4]}</Link>

        </div>


      </div>
      <Switch>
        <Route path={ "/home"} exact render={propRouter =>
          <Home {...propRouter} {...props} />
        } />
        <Route path={ "/warranty"} render={propRouter =>
          <WarrantyPage {...propRouter} {...props} />
        } />
        <Route path={ "/contacts"} render={propRouter =>
          <Contacts {...propRouter} {...props} />
        } />
        <Route component={NoFound} render={propRouter =>
          <NoFound {...propRouter} {...props} />
        } />
      </Switch>

    </div>
    </Grid>
    </div>
  )
}

export default withStyles(mainPageStyles)(Heder);